import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import 'rxjs/add/operator/debounceTime';

/**
 * Generated class for the DebounceSearchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-debounce-search',
  templateUrl: 'debounce-search.html',
})
export class DebounceSearchPage {
  items2 = [
    { title: "one" },
    { title: "two" },
    { title: "three" },
    { title: "four" },
    { title: "five" },
    { title: "six" }
  ];
  searchTerm: string = '';
  searchControl: FormControl;
  items: any;
  searching: any = false;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.searchControl = new FormControl();
  }

  ionViewDidLoad() {
    this.setFilteredItems("")
    this.searchControl.valueChanges.debounceTime(700).subscribe(search => {
        console.log(search)
        this.searching = false;
        this.setFilteredItems(search);

    });


}

onSearchInput(){
  this.searching = true;
}

setFilteredItems(search) {

  this.items = this.filterItems(search);

}

  filterItems(searchTerm) {
    console.log(this.items)
    return this.items2.filter(item => {
      return item.title.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
    });
  }

}



